<template>
    <div class="lazy-load">
        <slot v-if="show"></slot>
        <slot name="tip" v-if="!show">
            <div :class="[maskClass ? maskClass : 'lazy-load-mask']" v-html="tip"></div>
        </slot>
    </div>
</template>

<style scoped>
    .lazy-load {
        position: relative;
        width: 100%;
    }
    .lazy-load-mask {
        width: 150px;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        text-align: center;
        color: #000000;
        font-size: 14px;
        line-height: 24px;
    }
</style>

<script type="text/babel">
    export default {
        name: 'LazyRender',
        data() {
            return {
                show: true, // 
            }
        },
        props: {
            data: Array,  // 
            maskClass: String, // 
            tip: {
                type: String,
                default() {
                    return ',...'
                }
            },
            time: { // 
                type: Number,
                default() {
                    return 10
                }
            },
            limit: {  // 
                type: Number,
                default() {
                    return 30
                }
            },
            trackByData: Boolean, // data
            immediately: Boolean, // 
        },
        created() {
            this.showLazy()
        },
        watch: {
            data() { // 
                if (this.trackByData) {
                    this.showLazy()
                }
            },
            // ,
            $route() {
                if (!this.trackByData) {
                    this.showLazy()
                }
            },
            // true,
            immediately() {
                if (this.immediately) {
                    this.showLazy()
                }
            },
        },
        methods: {
            /**
             * ,loaded
             */
            showLazy() {
                if ((this.data && this.data.length > this.limit) || !this.data) {  // , ,
                    this.syncLoader()
                } else {  // ,
                    this.show = true
                    this.$emit('loaded')
                }
            },
            /**
             * 
             */
            syncLoader() {
                let _this = this;
                _this.show = false
                setTimeout(() => {
                    _this.show = true
                    _this.$emit('loaded')
                }, _this.time)
            }
        }
    };
</script>


